.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 320px;
  height: 122px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
  margin-left: 100px;
}

.subtotal_items_para {
  display: flex;
}

.subtotal_gift {
  display: flex;
  align-items: center;
}

.subtotal_gift > input {
  margin-right: 5px;
}
.subtotal > button {
  transition: 0.3s;
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}
.subtotal_button {
  transition: 0.3s;
}
.subtotal_button:hover {
  cursor: pointer;
  transform: translate(0, -1.5px);
}
