.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header_logo {
  transition: 0.3s;
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}
.header_logo:hover {
  transform: translate(0, -0.8px);
}

.header_search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}

.header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header_searchIcon {
  transition: 0.3s;
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
}

.header_searchIcon :hover {
  transform: translate(0, -1px);
  cursor: pointer;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
}

.header_option {
  display: flex;
  transition: 0.3s;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}
.header_option:hover {
  transform: translate(0, -1px);
  color: #f0c14b;
}

.header_optionLineOne {
  font-size: 10px;
}

.header_optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}

.header_optionBasket {
  display: flex;
  align-items: center;
  color: white;
  margin-right: 25px;
}
.header_optionBasket:hover {
  color: #f0c14b;
}
