.payment {
  background-color: white;
}
.payment_container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}

.payment_container > h1 > a {
  text-decoration: none;
}

.payment_title > h3 {
  text-align: left;
}

.payment_section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}

.payment_title {
  flex: 0.3;
}

.payment_address {
  flex: 0.11;
}

.payment_item,
.payment_details {
  display: flex;
  flex-direction: column;
}
.payment_item {
  margin-left: 380px;
}

.payment_priceContainer {
  text-align: right;
  margin-top: 20px;
  margin-left: 1px;
}
.payment_details > form {
  max-width: 400px;
}
.payment_details > h3 {
  padding-bottom: 20px;
}
.payment_details > form > div > button {
  transition: 0.3s;
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  font-weight: bolder;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
.payment_details > form > div > button:hover{
  cursor: pointer;
  transform: translate(0,-1.5px);
}